@use "../index.scss" as *;

label {
  position: relative;
}

.input {
  background: $very-light-greyish-cyan;
  border: none;
  width: 100%;
  border-radius: 5px;
  padding: 0.5rem;
  text-align: right;
  cursor: pointer;
  margin-top: 1rem;
  -moz-appearance: textfield;

  &::-webkit-inner-spin-button,
  ::-webkit-outer-spin-button {
    -webkit-appearance: none;
  }

  &:focus {
    outline: 2px solid $strong-cyan;
  }
}

.custom__tip {
  width: 147px;
  height: 48px;
  border: none;
  border-radius: 5px;
  background: $very-light-greyish-cyan;
  color: $dark-greyish-cyan;
  text-align: center;
  cursor: pointer;
  -moz-appearance: textfield;

  &::-webkit-inner-spin-button,
  ::-webkit-outer-spin-button {
    -webkit-appearance: none;
  }

  &:focus {
    outline: 2px solid $strong-cyan;
  }
}

.icon {
  position: absolute;
  top: 0;
  bottom: 0;
  transform: translateY(-50%);
  left: 20px;
}
